	module.exports = {"data":{"markdownRemark":{"html":"<p>Проверка на ввод на примере массива, используя цикл wile, методы cin.clear() И cin.get()\n[cut]</p>\n<p>[image=http://stud.ashcherbakov.ru/uploads/<em>pages/35/mini/2015-04-04</em>13h21<em>03.jpg 2015-04-04</em>13h21_03.jpg]<a href=\"http://stud.ashcherbakov.ru/uploads/_pages/35/2015-04-04_13h21_03.jpg%5B/image\">http://stud.ashcherbakov.ru/uploads/<em>pages/35/2015-04-04</em>13h21_03.jpg[/image</a>]</p>\n<p>[pre]        // Заполним массив\nfor ( int i = 0; i &#x3C; 10; i++) //строго, т.к. всего 10 элементов, нумерация же идёт с нулевого эл-та массива (0-9)\n{\ncout &#x3C;&#x3C; \"Введите значение \" &#x3C;&#x3C; i &#x3C;&#x3C; \" элемента массива: \\t\" ; // ввод символа\nwhile (!(cin >> A[i])) // пока ввод не удался\n{\ncin.clear(); // очищаем поток\nwhile (cin.get() != '\\n'); // пока пользователь не введёт символ, который система может распознать &#x26;&#x26; который не является переносом строки</p>\n<pre><code>                 cout &#x3C;&#x3C; \"Введите значение \" &#x3C;&#x3C; i &#x3C;&#x3C; \" элемента массива: \\t\" ;\n           }\n\n   }\n</code></pre>\n<p>[/pre]</p>","frontmatter":{"date":"2016-02-08","path":"/page/prostaja-proverka-na-vvod","title":"Простая проверка на ввод С++"}}},"pathContext":{}}

